<template>
    <el-container>
		<!--具体的评论内容-->
        <el-main style="margin-top: 30px;margin-bottom: 30px;width: 1000px;">
			<com @func="getObj"></com>
			<div v-if="phone">
				<h3 style="font-weight: 400;margin-top: 30px;">我的评论</h3>
				<div v-for="item in cts"
					style="position: relative;border-top: 1px solid rgb(240,240,242);
					border-bottom: 1px solid rgb(240,240,242);padding: 15px 0;font-size: 15px;width: 95%;">
					<div v-if="item.phone==phone">
						<div>
							<!--头像显示-->
							<el-avatar icon="el-icon-user-solid" style="width: 50px;height: 50px;border-radius: 25px;"></el-avatar>
						    <!--用户名、评论的内容以及回复的内容-->
						    <div style="margin-left: 70px;margin-top: -60px;">
						        <p><span style="color: #4d99de;">{{item.phone}}</span>: {{item.comment}}</p>
								<!--改变发表评论时间的格式-->
						        <p style="display:block;font-size: 13px;color: gray;opacity: 0.7">{{item.date | dateFormat2("yyyy-mm-dd HH:MM:SS")}}</p>
						    </div>
						</div>
					</div>
				</div>
			</div>
            <h3 style="font-weight: 400;margin-top: 30px;">全部评论({{total}})</h3>
			<!--所有的评论信息-->
            <div v-for="item in commentList"
                 style="position: relative;border-top: 1px solid rgb(240,240,242);
                 border-bottom: 1px solid rgb(240,240,242);padding: 15px 0;font-size: 15px;width: 95%;">
                <div>
                    <!--头像显示-->
                    <img :src="item.user.avatarUrl" alt=""
                         style="display: inline;width: 50px;height: 50px;border-radius: 25px;">
                    <!--用户名、评论的内容以及回复的内容-->
                    <div style="margin-left: 70px;margin-top: -60px;">
                        <p><span style="color: #4d99de;">{{item.user.nickname}}</span>: {{item.content}}</p>
						<!--回复的评论-->
						<!-- 首先判断是否有回复评论,如果有就显示出来,否则就不显示 -->
                        <div v-if="item.beReplied.length > 0"
                             style="background-color: rgb(241,241,244);padding: 5px 5px;">
                            <p v-for="reply in item.beReplied">
								<span style="color: #4d99de;">@{{reply.user.nickname}}</span>: {{reply.content}}
							</p>
                        </div>
						<!--改变发表评论时间的格式-->
                        <p style="display:block;font-size: 13px;color: gray;opacity: 0.7">{{item.time|dateFormat}}</p>
                    </div>
                </div>
            </div>
            <!--对评论进行分页-->
			<!-- 一页显示20条评论 -->
			<el-pagination style="text-align: center;margin-top:15px;" background layout="prev, pager, next" 
			:total="total" :page-size="20" :current-page="currentPage" @current-change="handleCurrentChange">
			</el-pagination>
        </el-main>
    </el-container>
</template>

<script>
	import com from './CommentHeader.vue'
    
	export default {
        data() {
            return {
                //所有评论的信息
                commentList: [],
                //评论的总条数
                total: 0,
				//当前评论的页数
				currentPage:1,
				//偏移量,用于分页
				offset:0,
				cts:[],
				phone:this.$store.state.phone,
            }
        },
        created() {			
            //获取所有评论信息
            this.getCommentList();	
			this.getObj();
        },
        methods: {
            //获取所有评论信息
            getCommentList() {
				var id=this.$store.state.id;
				var offset=this.offset;
				var that=this;
                this.$axios.get("https://autumnfish.cn/comment/playlist?id="+id+"&offset="+offset)
				.then((res) => {
                    that.commentList = res.data.comments;
                    that.total = res.data.total;
                })
            },
            //分页功能的实现
            handleCurrentChange(currentPage) {
                this.offset= (currentPage - 1) * 20;
                this.getCommentList();
            },
			getObj(){
				//直接从localStorage里获取值
				var cts=JSON.parse(localStorage.getItem("listObj")||'[]');
				this.cts=cts;
			}
        },
		components:{
			com
		}
    }
</script>

<style scoped>

element.style {
    margin-bottom: 60px;
    margin-top: 20px;
    margin-left: 65px;
}
</style>