<template>
	<div style="margin-top: -15px;">
		<el-input type="textarea" :rows="2" maxlength="140" show-word-limit placeholder="评论"
		  v-model="comment" style="width: 1000px;">
		</el-input>
		<el-button type="primary" size="medium" style="margin-left: 902px;" plain @click="submit">提交评论</el-button>
	</div>
</template>

<script>
	export default {
		data(){
			return{
				comment:"",
			}
		},
		methods:{
			submit(){
				//判断是否登录
				var phone=this.$store.state.phone;
				if(phone!=""){
					var obj={phone:phone,comment:this.comment,date:Date.now()};
					//localStorage 浏览器本地仓库 将数据保存到浏览器本地 getItem() setItem()
					//1.把原有的数据拿出来 封装成list
					var list=JSON.parse(localStorage.getItem("listObj")||'[]');
					//2.将新数据存入 list
					list.unshift(obj);
					//3.将更新过的list 重新保存在localStorage
					localStorage.setItem("listObj",JSON.stringify(list));
					this.comment="";
					this.$emit("func");
				}else{
					this.$router.push({
						name:"login",
					})
				}
			}
		}
	}
</script>

